h1{
  text-align: center;
  flex-direction: row;
  flex-wrap: wrap;
}

input {
  width: 100%;
  font-size:  1.2rem;
  color:black;
  margin: 20px;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  width: 100%;
  height: 300px;
}